<template>
  <div class="flex">
    <button
      v-for="option of props.options"
      :key="option.label"
      class="button relative border border-gray-200 px-3 py-2 transition-colors first:rounded-l-sm last:rounded-r-sm not-first:-ml-px dark:border-gray-400"
      :class="{
        'grow-1 basis-0': props.equalWidth,
        'before:opacity-0': option.value !== value,
        'bg-blue-50 before:opacity-100 dark:bg-blue-700/10': option.value === value,
      }"
      @click.prevent="value = option.value"
    >
      {{ option.label }}
    </button>
  </div>
</template>

<script setup lang="ts" generic="Value">
export type ToggleButtonsOption<T> = {
  label: string
  value: T
}

const props = defineProps<{
  options: ToggleButtonsOption<Value>[]
  equalWidth?: boolean
}>()

const value = defineModel<Value>()
</script>

<style lang="scss" scoped>
.button:before {
  content: "";
  z-index: 1;
  border-width: 1px;
  border-style: solid;
  inset: -1px;
  position: absolute;

  @apply border-blue-700;
  @apply first:rounded-l-sm;
  @apply last:rounded-r-sm;
  @apply transition-all;
}
</style>
